@import "bourbon";
@import "neat";
@import "base";
@import "variables";

/* HEADER */
.header-outer-container {
    background: url('assets/images/header.jpg') top center no-repeat;
    .top-bar {
        @include outer-container;
    }
    .daftar-kontak {
        @include span-columns(10);
        color: #fff;
        font-weight: 700;
        padding-top: 36px;
        text-align: right;
        ul {
            list-style: none;
        }
        li {
            display: inline-block;
            margin-left: 16px;
        }
    }
    .header-container {
        @include outer-container;
        padding: 60px 0;
        position: relative;
    }
    h1 {
        color: #fff;
    }
    .header-text {
        @include span-columns(12);
        color: #fff;
        padding: 60px 0;
        h1 {
            color: #fff;
            font-size: 92px;
            font-weight: 700;
            letter-spacing: -1px;
            line-height: 1.15;
            margin-bottom: 20px;
        }        
        ul {
            margin-bottom: 30px;
            margin-left: 0;
            list-style: none;
        }
        li {
            font-size: 24px;
            font-weight: 700;
        }
    }
}

.site-logo {
    @include span-columns(2);
    @include omega;
    font-family: 'Exo', sans-serif;
    font-weight: 900;
    letter-spacing: -1px;
    h1 {
        color: #fff;
    }
}

/* ABOUT */
.about-outer-container {
    background: #f0f4f7;
}
.about-container {
    @include outer-container;
    border-top: 4px solid #B2100E;
    padding: 40px 0 60px;
    .about {
        @include span-columns(12);
    }
    h2 {
        font-size: 48px;
        text-align: center;
    }
    h3 {
        font-size: 32px;
    }
}
.expander {
    @include span-columns(12);
    $base-font-size: 1em !default;
    $base-line-height: 1.5em !default;
    $base-spacing: 1.5em !default;
    $action-color: #477DCA !default;
    $dark-gray: #333 !default;
    $light-gray: #DDD !default;
    $base-font-color: $dark-gray !default;
    $expander-arrow-width: 0.7em;
    $expander-toggle-size: 1em;
    $expander-toggle-arrow-size: $expander-toggle-size;
    $expander-toggle-margin: 1em;
    
  
    .expander-trigger {
        background: #ebf3f5;
        color: #373739;
        cursor: pointer;
        display: block;
        margin-bottom: 1em;
        padding-bottom: 0.25em;
        text-decoration: none;
        user-select: none;
        padding: 10px;
        font-weight: 700;
  
      &::before {
        content: "\25BC";
        font-size: $expander-arrow-width;
        margin-right: 0.5em;
      }
    }
  
    .expander-content p {
      color: $base-font-color;
      line-height: $base-line-height;
      padding: 10px 30px;
    }
  
    .expander-hidden {
      &::before {
        content: "\25BA";
        font-size: $expander-arrow-width;
        
      }
    }
  
    .expander-hidden + .expander-content {
      display: none;
    }
  }

/* HARGA */
.harga {
    @include outer-container;
    color: #373739;
    padding: 140px 0;
    h2 {
        font-size: 48px;
        text-align: center;
        color: #373739;
    }
    .title-desc {
        text-align: center;
    }
    ul {
        list-style: none;
        margin-top: 20px;
    }
    li {
        border-bottom: 1px solid #f5f5f5;
        font-size: 16px;
        padding: 8px;
        &:last-child {
            border-bottom: none;
        }
    }
    small {
        color: #c7c7c7;
    }
    h3, h4 {
        color: #373739;
        font-weight: 900;
        margin-top: 0;
        margin-bottom: 0;
    }
}
.starter, .profesional, .bisnis {
    @include span-columns(4 of 12);
    @include pad;
    text-align: center;
    .button-pesan {
        display: inline-block;
        height: 78px;
        padding: 0 40px;
        text-align: center;
        font-size: 17px;
        font-weight: 700;
        line-height: 68px;
        letter-spacing: .1rem;
        text-transform: uppercase;
        text-decoration: none;
        white-space: nowrap;
        cursor: pointer;
        box-sizing: border-box; 
        color: #020918;
        border: 4px solid #020918;
        background: transparent;
        &:hover {
            color: #fff;
            background: #B2100E;
            border: 4px solid transparent;
        }
    }
}

