        font-size: 17px;
        font-weight: 700;
        line-height: 68px;
        letter-spacing: .1rem;
        text-transform: uppercase;
        text-decoration: none;
        white-space: nowrap;
        cursor: pointer;
        box-sizing: border-box; 
        color: #020918;
        border: 4px solid #020918;
        background: transparent;
        &:hover {
            color: #fff;
            background: #B2100E;
            border: 4px solid transparent;
        }
    }
}

/* FOOTER */
.images-set {
    background: #B2100E;
    .footer-images {
        display: inline-block;
        width: 25%;
        opacity: .5;
        font-size: 0;
    }
}
.footer-outer-container {
    background: #B2100E;
    color: #fff;
    a {
        color: #fff;
    }
    .footer-content {
        @include outer-container;
        padding: 80px 0;
    }
}
.copyright {
    @include span-columns(10);
    padding-top: 26px;
    text-align: right;
    p {
        font-size: 12px;
    }
}

/* BUTTON */
.button,
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  display: inline-block;
  height: 78px;
  padding: 0 60px;
  text-align: center;
  font-size: 17px;
  font-weight: 700;
  line-height: 68px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  box-sizing: border-box; 
  color: #FFF;
  border: 4px solid #FFF;
  background: transparent;
  &:hover {
      color: #020918;
      background: #fff;
      border: 4px solid transparent;
  }
}

/* TOOLTIP */
.tooltip-item {
    $base-border-color: gainsboro !default;
    $base-border-radius: 3px !default;
    $base-line-height: 1.5em !default;
    $dark-gray: #333 !default;
    $base-font-color: $dark-gray !default;
    $tooltip-background: white;
    $tooltip-color: $base-font-color;
    $tooltip-max-width: 16em;
    $tooltip-arrow-width: 8px;
    $tooltip-shadow: 0 2px 2px rgba(143, 143, 143, 0.411);
    $tooltip-distance-from-item: 3em;
    $tooltip-arrow-distance-from-box: -1.3em;
  
    display: inline-block;
    position: relative;
    text-align: center;
  
    &:focus,
    &:hover .tooltip {
      opacity: 1;
      visibility: visible;
    }
  
    .tooltip {
      @include position(absolute, null 0 $tooltip-distance-from-item 0);
      position: absolute;
    right: 0;
    bottom: 3em;
    left: -85px;
    background-color: white;
    background: #fff;
    border-radius: 3px;
    color: #333;
    font-size: 0.7em;
    width: 200px;
    opacity: 0;
    padding: 1em;
    text-align: center;
    transition: all 0.2s ease-in-out;
    visibility: hidden;
    z-index: 10;
    box-shadow: $tooltip-shadow;
  
      p {
        color: $base-font-color;
        line-height: $base-line-height;
        margin: 0;
      }
  
      &::after {
        @include position(absolute, null 0);
        border: $tooltip-arrow-width solid transparent;
        bottom: $tooltip-arrow-distance-from-box;
        color: $tooltip-background;
        content: "â–¼";
        font-size: 1.4em;
        margin-left: -$tooltip-arrow-width;
        text-align: center;
        text-shadow: $tooltip-shadow;
      }
    }
  }

  /* FORM */
  #honeypot {
    display: none; /*makes the field not visible to humans*/
  }

  .pure-group {
      border: 0;
      padding: 0;
      margin-bottom: 10px;
  }

  form input, form textarea, form select {
      display: block;
      border: none;
      padding: 14px 10px;
      width: 100%;
      border: 1px solid #cbd0d1;
  }

  form button {
    display: block;
    margin-top: 20px;
    width: 100%;
  }

  #thankyou_message {
      text-align: center;
  }